@import url('https://fonts.googleapis.com/css?family=Nunito|PT+Sans');

#input-description {
  color: var(--p-color);
  text-align: center;
}

#text-and-submit {
  opacity: 0.8;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

#text-input {
  padding: 10px;
  width: 60%;
}

#submit-button {
  color: white;
  padding: 12px;
  font-weight: bold;
  background: -webkit-radial-gradient(bottom, #4923bf 10%, #2840c1 60%);
  background: -o-radial-gradient(bottom, #4923bf 10%, #2840c1 60%);
  background: radial-gradient(bottom, #4923bf 10%, #2840c1 60%);
  border: none;
  -webkit-box-shadow: 0px 0px 20px 0px #2840c1;
          box-shadow: 0px 0px 20px 0px #2840c1;
  cursor: pointer;  
  text-transform: uppercase;  
}

#submit-button:hover {
  background: -webkit-radial-gradient(bottom, #4923bf 10%, #344cc2 60%);
  background: -o-radial-gradient(bottom, #4923bf 10%, #344cc2 60%);
  background: radial-gradient(bottom, #4923bf 10%, #344cc2 60%);
}




